import React,{ useState} from 'react';
import './Contact.css';
import contactImg from "./img/map.jpg"
//import react,{ useState} from "react";
function Contact() {
    const [ userData,setUserData] = useState({
        name: "",
        email: "",
        subject: "",
        message: "",
        phone:"",
    });
let name, value;
const postUserData = (event) => {
    name = event.target.name;
    value = event.target.value;

    setUserData({ ...userData, [name]: value });

}
//connect with firebase
    const submitData = async (event) => {
        event.preventDefault();
        const {name, email, subject, message, phone } = userData;
        if(name && email && subject && message && phone ){

        
       const res = fetch('https://l2cl-75176-default-rtdb.firebaseio.com/L2ClRecords.json',
        {
        method: "POST",
        Headers: {
            "Content-type" :"application/json",
        },
        body:JSON.stringify({
            name,
            email,
             subject,
             message,
             phone
        })
    }
        );
        if (res) {
        setUserData({
        name: "",
        email: "",
        subject: "",
        message: "",
        phone:"",
        })
            alert("Data Sumbited");
        }else{
            alert("plz fill the Data");
        }
    }else{
        alert("plz fill the Data");
    }
    };
    return (
        <div className="contact component__space" id="Contact">
            <div className="row">
                <div className="col__2">
                    <div className="contact__box">  
                        <div className="contact__meta">
                            <h1 className="hire__text">Join Us.</h1>
                            <p className="hire__text white"> Connect with me via phone:</p>
                           <p className="hire__text white"> <a href="tel:+919509738551"><strong>+91 95097 38551</strong></a> or email <a href="mailto:l2clcdc@gmail.com"><strong>l2clcdc@gmail.com</strong></a></p>
                        </div>
                        <div className="input__box">
                           <input type="text" className="contact name" placeholder="Your name *" value={userData.name} onChange={postUserData} name='name' />
                           <input type="text" className="contact email" placeholder="Your Email *" value={userData.email} onChange={postUserData} name='email' />
                           <input type="text" className="contact subject" placeholder="Write a Subject" value={userData.subject} onChange={postUserData} name='subject' />
                           <input type="tel" className="contact subject" placeholder="Write a Number" value={userData.phone} onChange={postUserData} name="phone" pattern="[0-9]{3}-[0-9]{2}-[0-9]{3}" />
                           <textarea name="message" id="message" placeholder="Write Your message" value={userData.message} onChange={postUserData} name='message' ></textarea>
                           <button className="btn contact pointer" type="submit" onClick={submitData}>Submit</button>
                        </div>
                    </div>
                </div>
                <div className="col__2 col__8">
                    <img src={contactImg} alt="" className="contact__img" />
                </div>
            </div>
        </div>
    )
}

export default Contact
